@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');



* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: 0;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #0b0c10; /* Background color */
    --color-bg-variant: #1f2833; /* Background variant */
    --color-primary: #66fcf1; /* Primary color */
    --color-primary-variant: #45A29E; /* Primary text color variant */
    --color-white: #fff; /* White color */
    --color-light: #c5c6c7e4; /* Light gray text */
    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;

}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

